/**
 * localStorage  sessionStorage 
 *
 *
 * @example
 * // 
 * import * as storage from 'path/to/storage';
 *
 * @example
 // keyPrefix
 storage.init({
    keyPrefix: user.id,
 });
 *
 * @example
 * // localStorage
 * storage.setItem('user', userObj);
 *
 * @example
 * // sessionStorage
 * import {session} from 'path/to/storage';
 * session.setItem('user', userObj);
 *
 *
 * @module 
 */

const localStorage = window.localStorage;
const sessionStorage = window.sessionStorage;
let _keyPrefix = 'default-prefix-';

/**
 * 
 * @param options keyPrefix
 */
export function init(options) {
    const {keyPrefix = 'default-prefix'} = options;
    _keyPrefix = `${keyPrefix}-`;
}

/**
 * localStorage 
 * @param {string} key key
 * @param {*} value 
 */
export function setItem(key, value) {
    key = _keyPrefix + key;
    value = JSON.stringify(value);
    localStorage.setItem(key, value);
}

/**
 * localStorage 
 * @param {string} key
 * @return {json} key 
 */
export function getItem(key) {
    key = _keyPrefix + key;
    let value = localStorage.getItem(key);
    return JSON.parse(value);
}

/**
 * localStorage keyPrefix
 */
export function clear() {
    const localStorageKeys = Object.keys(localStorage);
    if (localStorageKeys && localStorageKeys.length) {
        localStorageKeys.forEach(item => {
            if (item.startsWith(_keyPrefix)) {
                localStorage.removeItem(item);
            }
        });
    }
}

/**
 * localStorage 
 * @param key
 */
export function removeItem(key) {
    key = _keyPrefix + key;
    localStorage.removeItem(key);
}

/**
 * localStorage keys 
 * @param {array} keys
 * @returns {{json}}
 */
export function multiGet(keys) {
    let values = {};
    keys.forEach(key => values[key] = getItem(key));
    return values;
}

/**
 * localStorage keys 
 * @param {array} keys
 */
export function multiRemove(keys) {
    keys.forEach(key => removeItem(key));
}

/**
 * sessitonStorage localStorage
 *
 * @type {{setItem(*=, *=): void, getItem(*=): *, clear(): void, removeItem(*=): void, multiGet(*): *, multiRemove(*): void}}
 */
export const session = {
    setItem(key, value) {
        key = _keyPrefix + key;
        value = JSON.stringify(value);
        sessionStorage.setItem(key, value);
    },
    getItem(key) {
        key = _keyPrefix + key;
        let value = sessionStorage.getItem(key);
        return JSON.parse(value);
    },
    //  keyPrefix 
    clear() {
        const sessionStorageKeys = Object.keys(sessionStorage);
        if (sessionStorageKeys && sessionStorageKeys.length) {
            sessionStorageKeys.forEach(item => {
                if (item.startsWith(_keyPrefix)) {
                    sessionStorage.removeItem(item);
                }
            });
        }
    },
    removeItem(key) {
        key = _keyPrefix + key;
        sessionStorage.removeItem(key);
    },
    multiGet(keys) {
        let values = {};
        keys.forEach(key => values[key] = this.getItem(key));
        return values;
    },
    multiRemove(keys) {
        keys.forEach(key => this.removeItem(key));
    },
};


const globalStorage = {};
/**
 * 
 * @type {{setItem(*, *): void, getItem(*): *, clear(): void, removeItem(*): void, multiGet(*): *, multiRemove(*): void}}
 */
export const global = {
    setItem(key, value) {
        key = _keyPrefix + key;
        globalStorage[key] = value;
    },
    getItem(key) {
        key = _keyPrefix + key;
        return globalStorage[key];
    },
    clear() {
        Object.keys(globalStorage).forEach(key => {
            if (key.startsWith(_keyPrefix)) {
                delete globalStorage[key];
            }
        });
    },
    removeItem(key) {
        key = _keyPrefix + key;
        delete globalStorage[key];
    },
    multiGet(keys) {
        let values = {};
        keys.forEach(key => values[key] = this.getItem(key));
        return values;
    },
    multiRemove(keys) {
        keys.forEach(key => this.removeItem(key));
    },
};
