import {notification} from 'antd';
import {toLogin} from './index';

/**
 *  errorTio > resData.message > error.message > ''
 *
 * @param error
 * @param errorTip
 * @returns {*}
 */
function getErrorTip({error, errorTip}) {
    const commonTip = '';

    if (errorTip && errorTip !== true) return errorTip;

    if (error && error.response) {
        const {status, message} = error.response;

        if (status === 403) {
            return '';
        }

        if (status === 404) {
            return '';
        }

        if (status === 504) {
            return commonTip;
        }

        if (status === 500) {
            return commonTip;
        }

        // 
        if (message) return message;
    }

    if (error && error.message && error.message.startsWith('timeout of')) return '';

    if (error) return error.message;

    return commonTip;
}

export default function handleError({error, errorTip}) {
    const {status} = error?.response || {};

    // 
    if (status === 401) return toLogin();

    if (errorTip === false) return;

    const description = getErrorTip({error, errorTip});

    notification.error({
        message: '',
        description,
        duration: 2,
    });
}
